  toggleDisplay = () => {
    let create = this.state.create
    this.setState({
      create: !create
    })
  }

  getEditSchedule = (index) => {
    this.setState({
      getEdit: true,
      editId: index,
    })
  }

  addNewSchedule = (newSchedule) => {
    let schedule = this.state.schedule
    let create = this.state.create
    this.setState({
      schedule: [newSchedule, ...schedule],
      create: !create
    })
  }

  submitEditSchedule = (editSchedule) => {
    let schedule = this.state.schedule
    let index = this.state.editId
    let getEdit = this.state.getEdit
    schedule.splice(index, 1, editSchedule)
    this.setState({
      schedule,
      getEdit: !getEdit,
      editId: null,
    })
  }

  render() {
    const { create, schedule, getEdit, editId } = this.state
    return (
      <div className='schedule'>
        {create ? null :
          <button
            className={getEdit ? 'hide' : null}
            onClick={this.toggleDisplay}>Dodaj</button>}
        {create ?
          <CreateSchedule
            schedule={schedule}
            addNewSchedule={this.addNewSchedule} /> : null}
        {getEdit ?
          <EditSchedule
            schedule={schedule[editId]}
            submitEditSchedule={this.submitEditSchedule} />
          :
          <Table
            schedule={schedule}
            getEdit={this.getEditSchedule} />
        }
        <Prompt
          when={create}
          message={'Masz niezapisany formularz. Czy na pewno chcesz opuci stron?'} />
      </div>
    );
  }
